// @/components/Template1.tsx
import React from "react";
import Header from "@/components/tempcomponent/Header"; // your Header component
// Import other section components as you build them
// import HeroSection from "@/components/sections/HeroSection";
// import Features from "@/components/sections/Features";
// etc.

interface Template1Props {
  data: {
    templateName: string;
    sections: { section: string; content: Record<string, any> }[];
    styles?: any;
  };
}

const sectionMap: Record<string, React.FC<{ content: any }>> = {
  header: Header,
  // hero: HeroSection,
  // features: Features,
  // Add others here when ready
};

export default function Template1({ data }: Template1Props) {
  const { sections = [] } = data.sections ? data : { sections: [] };

  return (
    <div className="min-h-screen bg-white">
      {sections.map(({ section, content }) => {
        const SectionComponent = sectionMap[section];

        if (!SectionComponent) {
          return (
            <div
              key={section}
              className="py-8 px-4 text-center text-gray-500 border-b"
            >
              Section "{section}" not implemented yet
            </div>
          );
        }

        // This is the key: pass ONLY content as prop
        return <SectionComponent key={section} content={content} />;
      })}
    </div>
  );
}